import React,{useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import FormControl from 'react-bootstrap/FormControl';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';
import DarkSpiderIcon from './components/DarkSpiderIcon';


const Register = ()=>{

  const [name,setName]=useState();
  const [midName,setMidName]=useState();
  const [lastname,setLastname]=useState();
  const [country,setCountry]=useState();
  const [username,setUsername]=useState();
  const [password,setPassword]=useState();
  const [confPass,setConfPass]=useState();
  const [company,setCompany]=useState();

  const handleName = (event)=>{
    setName(event.target.value);
  }

  const handleMidName = (event)=>{
    setMidName(event.target.value);
  }

  const handleLastname = (event)=>{
    setLastname(event.target.value);
  }

  const handleCountry = (event)=>{
    setCountry(event.target.value);
  }

  const handleUsername = (event)=>{
    setUsername(event.target.value);
  }

  const handleConffPass= (event)=>{
    setConfPass(event.target.value);
  }

  const handlePassword = (event)=>{
    setPassword(event.target.value);
  }

  const handleCompany = (event)=>{
    setCompany(event.target.value);
  }


  const handleSubmit = ()=>{
    const body = new FormData();
    body.append('name',name);
    body.append('middlename',midName);
    body.append('lastname',lastname);
    body.append('country',country);
    body.append('username',username);
    body.append('password',password);
    body.append('confirm_password',confPass)
    body.append('company',company);


    fetch('http://localhost:7555/api/register',{
      method : 'POST',
      body : body,
      mode : 'cors'
    })
    .then((res)=>{
      res.json();
    })
    .then((res)=>{
      console.log(res)
    })
    .catch((error)=>{
      console.log(error);
    })

  }

  return(
    <Container fluid
      className = {'h-100'}
      style = {{
      background : 'rgb(28,28,28)',
      background : 'linear-gradient(90deg, rgba(28,28,28,1) 8%, rgba(38,80,94,1) 65%, rgba(63,146,164,1) 91%)',
      minHeight : 800,
      padding : 100
      }}
      >

          <Row className="justify-content-md-center h-100" >

            <Col xs lg="4" style = {{marginTop : 50}}>
            <div style = {{backgroundColor : '#fff', opacity : 0.8,padding : 50,borderRadius:10}}>
              <DarkSpiderIcon color={'black'}/>

              <InputGroup size="md" className="mb-3" onChange = {handleName}>
                <FormControl placeholder = 'Name:' />
              </InputGroup>

              <InputGroup size="md" className="mb-3" onChange = {handleMidName}>
                <FormControl placeholder = 'Middle Name:' />
              </InputGroup>

              <InputGroup size="md" className="mb-3" onChange = {handleLastname}>
                <FormControl placeholder = 'Lastname:' />
              </InputGroup>

              <InputGroup size="md" className="mb-3" onChange = {handleUsername}>
                <FormControl placeholder = 'Username:' />
              </InputGroup>

              <InputGroup size="md" className="mb-3" onChange = {handleCountry}>
                <FormControl placeholder = 'Country:' />
              </InputGroup>


              <InputGroup size="md" className="mb-3" onChange = {handlePassword} >
                <FormControl placeholder = 'Password:' type = {'password'}/>
              </InputGroup>

              <InputGroup size="md" className="mb-3" onChange = {handleConffPass}>
                <FormControl placeholder = 'Confirm Password:' />
              </InputGroup>

              <InputGroup size="md" className="mb-3" onChange = {handleCompany}>
                <FormControl placeholder = 'Company:' />
              </InputGroup>

              <Button  as="input" variant="outline-dark" type="button" value="Submit" size = 'md' onClick ={handleSubmit} />



            </div>
            </Col>
          </Row>

    </Container>

  );
}

export default Register;
