import React,{useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Badge from 'react-bootstrap/Badge';
import ElementBadge from './ElementBadge';


const ElementsField = ({array,setArray})=>{

  const removeElement = (index)=>{
    if(index > -1){
        let arrayMod = Object.assign([],array);
        arrayMod.splice(index,1);
        setArray(arrayMod);


      }
  }

  const handleElements = (item,index)=>{
    return <ElementBadge item = {item} index = {index} rmElement = {removeElement}/>
  }


  return(
    <div
    style = {{padding : 5,
              background : ' background:#7f7f7f',
              background: 'rgba(255,255,255,0.1)',
              marginBottom : 20,
              borderRadius :5
            }}>


      { array.map(handleElements) }




    </div>


  );


}

export default ElementsField ;
