import React,{useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import FormControl from 'react-bootstrap/FormControl';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';
import DarkSpiderIcon from './components/DarkSpiderIcon';


const LogIn = ()=>{

  const [username,setUsername]=useState();
  const [password,setPassword]=useState();

  const handleUsername = (event)=>{
    setUsername(event.target.value);
  }

  const handlePassword = (event)=>{
    setPassword(event.target.value);
  }

  const handleSubmit = ()=>{
    const body = new FormData();
    body.append('username',username);
    body.append('password',password);


    fetch('http://localhost:7555/api/login',{
      method : 'POST',
      body : body,
      mode : 'cors'
    })
    .then((res)=>{
      res.json();
    })
    .then((res)=>{
      console.log(res)
    })
    .catch((error)=>{
      console.log(error);
    })

  }

  return(
    <Container fluid
      className = {'h-100'}
      style = {{
      background : 'rgb(28,28,28)',
      background : 'linear-gradient(90deg, rgba(28,28,28,1) 8%, rgba(38,80,94,1) 65%, rgba(63,146,164,1) 91%)',
      minHeight : 800,
      padding : 100
      }}
      >

          <Row className="justify-content-md-center h-100" >

            <Col xs lg="4" style = {{marginTop : 50}}>
            <div style = {{backgroundColor : '#fff', opacity : 0.8,padding : 50,borderRadius:10}}>
              <DarkSpiderIcon color={'black'}/>
              <InputGroup size="md" className="mb-3" onChange = {handleUsername}>
                <FormControl placeholder = 'Username' />
              </InputGroup>

              <InputGroup size="md" className="mb-3" onChange = {handlePassword} >
                <FormControl placeholder = 'Password' type = {'password'}/>
              </InputGroup>
              <Button  as="input" variant="outline-dark" type="button" value="Submit" size = 'md' onClick ={handleSubmit} />



            </div>
            </Col>
          </Row>

    </Container>

  );
}

export default LogIn;
